@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,400);
body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Dog img {
  width: 75%;
  -webkit-filter: grayscale();
          filter: grayscale();
  border-radius: 50%;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  transition: 0.4s -webkit-filter ease;
  transition: 0.4s filter ease;
  transition: 0.4s filter ease, 0.4s -webkit-filter ease;
}
.Dog:hover img {
  -webkit-filter: none;
          filter: none;
}

.Dog .underline {
  position: relative;
  display: inline-block;
  font-size: 1.4rem;
  letter-spacing: 0.2em;
  text-align: center;
  text-decoration: none;
  line-height: 36px;
  text-transform: uppercase;
  vertical-align: top;
  color: black;
  margin: 10px;
  border: 4px solid transparent;
}

.underline::after {
  width: 0%;
  height: 4px;
  display: block;
  background-color: #17a2b8;
  content: " ";
  position: absolute;
  top: 34px;
  left: 50%;
  border-radius: 10px;
  transition: left 0.4s cubic-bezier(0.215, 0.61, 0.355, 1),
    width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.Dog:hover .underline::after {
  width: 100%;
  top: 34px;
  left: 0;
}

.DogDetails-card {
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.DogDetails-card img {
  -webkit-filter: grayscale();
          filter: grayscale();
  transition: 0.4s -webkit-filter ease;
  transition: 0.4s filter ease;
  transition: 0.4s filter ease, 0.4s -webkit-filter ease;
}
.DogDetails-card:hover img {
  -webkit-filter: none;
          filter: none;
}

.App {
  text-align: center;
}

